
.parent-form{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;       
}
.form
{
    height: 350px;
    width: 400px;
    background-color: rgb(151, 231, 231); 
    text-align: center;
   border-radius: 25px;
   border: 2px solid black;
}

.input
{
    height: 27px;
    width: 150px;
border-radius: 6px;  
border: none;
}
label
{
    font-weight: bolder;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.signup-btn{
    font-weight: bolder;
    width: 80px;
    height: 30px;
    background-color: rgb(138, 166, 166);
    color: rgb(138, 30, 226);
    border: none;
    font-weight: bolder;
}

@media only screen and (max-width:450px){

    .form{
        width: 100%;
        height: 70%;
        position: absolute;
        top: 80px;
        
    }


}